import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable, of, throwError } from 'rxjs';
import { map, tap, catchError, finalize, share } from 'rxjs/operators';

import { AvatarConfigService } from './avatar-config.service';
import { AvatarSource } from './sources/avatar-source.enum';

/**
 * list of Supported avatar sources
 */
export const defaultSources = [
  AvatarSource.FACEBOOK,
  AvatarSource.GOOGLE,
  AvatarSource.TWITTER,
  AvatarSource.VKONTAKTE,
  AvatarSource.SKYPE,
  AvatarSource.GRAVATAR,
  AvatarSource.GITHUB,
  AvatarSource.CUSTOM,
  AvatarSource.INITIALS,
  AvatarSource.VALUE
];

/**
 * list of default colors
 */
export const defaultColors = [
  '#1abc9c',
  '#3498db',
  '#f1c40f',
  '#8e44ad',
  '#e74c3c',
  '#d35400',
  '#2c3e50',
  '#7f8c8d'
];

/**
 * Default request cache lifetime
 */
export const defaultCacheLifetimeSecond = 30 * 60;

interface ICacheEntry {
  avatar?: any;
  error?: any;
}

/**
 * Provides utilities methods related to Avatar component
 */
@Injectable()
export class AvatarService {
  public avatarSources: AvatarSource[] = defaultSources;
  public avatarColors: string[] = defaultColors;
  public cacheLifetimeSecond: number = defaultCacheLifetimeSecond;

  private cache: { [url: string]: ICacheEntry } = {};
  private requestCache: { [url: string]: Observable<any> } = {};

  constructor(
    private http: HttpClient,
    private avatarConfigService: AvatarConfigService
  ) {
    this.overrideAvatarSources();
    this.overrideAvatarColors();
    this.overrideCacheLifetime();
  }

  public fetchAvatar(avatarUrl: string): Observable<any> {
    if (this.cache[avatarUrl]) {
      return this.cache[avatarUrl].error
        ? throwError(this.cache[avatarUrl].error)
        : of(this.cache[avatarUrl].avatar);
    }

    if (this.requestCache[avatarUrl]) return this.requestCache[avatarUrl];

    this.requestCache[avatarUrl] = this.http
      .get(avatarUrl)
      .pipe(
        tap(a => {
          delete this.requestCache[avatarUrl];
          this.cache[avatarUrl] = {
            avatar: a,
          };
        }),
        catchError(e => {
          delete this.requestCache[avatarUrl];
          this.cache[avatarUrl] = {
            error: e,
          };
          return throwError(e);
        }),
        finalize(() => {
          setTimeout(() => delete this.cache[avatarUrl], this.cacheLifetimeSecond * 1000);
        }),
        share()
      );

    return this.requestCache[avatarUrl];
  }

  public getRandomColor(avatarText: string): string {
    if (!avatarText) {
      return 'transparent';
    }
    const asciiCodeSum = this.calculateAsciiCode(avatarText);
    return this.avatarColors[asciiCodeSum % this.avatarColors.length];
  }

  public copmareSources(
    sourceType1: AvatarSource,
    sourceType2: AvatarSource
  ): number {
    return (
      this.getSourcePriority(sourceType1) - this.getSourcePriority(sourceType2)
    );
  }

  public isSource(source: string): boolean {
    return this.avatarSources.includes(source as AvatarSource);
  }

  public isTextAvatar(sourceType: AvatarSource): boolean {
    return [AvatarSource.INITIALS, AvatarSource.VALUE].includes(sourceType);
  }

  private overrideAvatarSources(): void {
    this.avatarSources = this.avatarConfigService.getAvatarSources(
      defaultSources
    );
  }

  private overrideAvatarColors(): void {
    this.avatarColors = this.avatarConfigService.getAvatarColors(defaultColors);
  }

  private overrideCacheLifetime(): void {
    this.cacheLifetimeSecond = this.avatarConfigService.getCacheLifetime(defaultCacheLifetimeSecond);
  }

  private calculateAsciiCode(value: string): number {
    return value
      .split('')
      .map(letter => letter.charCodeAt(0))
      .reduce((previous, current) => previous + current);
  }

  private getSourcePriority(sourceType: AvatarSource) {
    return this.avatarSources.indexOf(sourceType);
  }
}
